* {
	box-sizing: border-box;
}

html,
body {
	overflow-x: hidden;
	margin: 0;
	padding: 0;
}

.App {
	overflow: hidden;
}

.hero-wrapper {
	background: #6c63ff;
	background: radial-gradient(circle, #6c63ff 0%, rgba(3, 3, 81, 1) 69%);
}

@media screen and (min-width: 1024px) {
	.text-wrapper {
		list-style-type: disc;
	}
}

.toggled-span {
	transform-origin: center;
}

.toggled-span.upper {
	transform-origin: 0% 0%;
}

.toggled-span.downer {
	transform-origin: 0% 100%;
}

.nav-menu {
	height: 0;
	transition: all 200ms ease-in-out;
	overflow: hidden;
}

.nav-menu.toggled {
	overflow: auto;
	height: 75vh;
	background-color: #020202;
}

.gymness-text {
	list-style: none;
}

.gymness-text li::before {
	content: 'üí™';
	margin-right: 0.5rem;
}

.input-join {
	flex-grow: 1;
}

.input-join::placeholder {
	color: #646464;
}

.classes-features {
	list-style: none;
}

.classes-features li {
	position: relative;
}

.classes-features li::before {
	content: '‚úÖ';
}

.testimonial {
	background-image: linear-gradient(45deg, #8e2de2, #4a00e0);
	width: 100%;
}

.testimonial .person-image {
	filter: grayscale(0.3);
}

.testimonial-text {
	position: relative;
}

.testimonial-text::before {
	content: open-quote;
}

.testimonial-text::after {
	content: close-quote;
}

.background-bmi {
	background-image: url(../src/assets/background-bmi.png);
}

.pricing-bullet {
	position: relative;
}

.pricing-bullet::before {
	content: '‚û°Ô∏è';
	margin-right: 0.5rem;
}

.modal {
	max-width: 30rem;
	width: 100%;
	background-color: #4a00e0;
}

.modal::backdrop {
	background-color: #030351;
	opacity: 0.3;
}

.footer-paragraph::before {
	content: open-quote;
}

.footer-paragraph::after {
	content: close-quote;
}
