.card {
	position: relative;
	height: 360px;
	width: 540px;
	cursor: pointer;
	transition: all 600ms linear;
}

.card::after {
	content: '';
	position: absolute;
	inset: 0;
	background: linear-gradient(
		0deg,
		var(--clr1),
		var(--clr2),
		var(--clr3),
		var(--clr4),
		var(--clr5)
	);
	border-radius: inherit;
	filter: blur(3.5rem);
}

.card .front,
.card .back {
	height: 100%;
	width: 100%;
	overflow: hidden;
	backface-visibility: hidden;
	position: absolute;
	transition: 750ms ease-in-out;
}

.front {
	transform: rotateX(0deg);
}

.front img {
	height: 100%;
}

.back {
	transform: rotateX(-180deg);
}

.card:hover .front {
	transform: rotateX(180deg);
}

.card:hover .back {
	transform: rotateX(0deg);
}
